.buttonImg {
  width: 50px;
  height: 50px;
  background-size: 50px 50px;
  background-position: center;
  background-repeat: no-repeat;
  border: none;
}

.buttonFlex {
  display: flex;
  height: 100px;
  align-items: center;
  align-self: center;
  padding-left: 36px;
  padding-right: 36px;
  padding-top: 39px;
}

.arrowFlexOne {
  height: 50px;
  display: flex;
  flex-direction: column;
}

.arrowFlexTwo {
  display: flex;
  flex-direction: column;
  padding-top: 8px;
}
