.border {
  width: 400px;
  height: 156px;

  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 0px 0px 8px 8px;
  border-top: none;

  padding: 24px 22px 16px 22px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.inputText {
  width: 142px;
  height: 42px;

  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 42px;

  color: #000000;
  border: none;
}
.inputText:focus-visible {
  outline: none;
}

.select {
  outline: "none";
  color: red;
  width: 400px;
  height: 44px;
  box-sizing: border-box;
  border-radius: 8px 8px 0px 0px;
  border: none;
  padding: "0px 0px 0px 0px";
}

.select:focus-visible {
  outline: 0;
}

.keysFont {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
  width: 40px;
}

.labelFont {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  color: #71767a;
  width: 250px;
  word-wrap: break-word;
  text-overflow: ellipsis;
  overflow: hidden;
  padding-left: 16px;
}

.infoFont {
  width: 160px;
  height: 19px;

  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;

  color: #71767a;
}

.content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.outputChapter {
  width: 400px;
  height: 200px;
  display: flex;
  flex-direction: column;
  padding-top: 16px;
}

.upperText {
  display: flex;
  flex-direction: column;
  height: 23px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  color: #71767a;
}

.selectCheckboxArrow {
  display: flex;
  justify-content: flex-end;
  width: 60px;
  height: 21px;
  padding-right: 9px;
}

.checkboxArrowSize {
  width: 13px;
  height: 10.5px;
}

.checkedArrowPos {
  padding-right: 9px;
}
